export default {
    namespaced: true,

    state: () => ({
        page: 0
    }),

    getters: {
        total: (state) => state.page
    },

    mutations: {
        upper(state, { value }) {
            state.page += 1;
            state.page = state.page % value;
        },

        lower(state, { value }) {
            state.page -= 1;
            if(state.page < 0) {
                state.page = value - 1;
            }
        }
    },

    actions: {
        goUp: ({ commit }, payload) => {
            commit("upper", payload);
        },

        goDown: ({ commit }, payload) => {
            commit("lower", payload);
        }
    }
};
