<template>
  <div>
    <div class="up">
      <span>{{ getUp }}</span>
      <img @click="changeComponent('up')" src="../assets/up-arrow.png" alt="" />
    </div>
    <div class="down">
      <span>{{ getDown }}</span>
      <img
        @click="changeComponent('down')"
        src="../assets/down-arrow.png"
        alt=""
      />
    </div>
  </div>
</template>

<script>
export default {
  props: ["Components"],
  computed: {
    getUp() {
      let str;

      if(this.$store.getters[`pager/total`] + 2 > this.Components.length) {
        str =  this.Components[0];
      }
      else {
        str =  this.Components[this.$store.getters[`pager/total`] + 1];
      }

      return str;
    },
    getDown() {
      let str;

      if(this.$store.getters[`pager/total`] - 1 < 0) {
        str = this.Components[this.Components.length - 1];
      }
      else {
        str = this.Components[this.$store.getters[`pager/total`] - 1];
      }

      return str;
    },
  },
  methods: {
    changeComponent(direction) {
      if(direction === 'up') {
        this.$store.dispatch(`pager/goUp`, {
          value: this.Components.length
        });
      } else {
        this.$store.dispatch(`pager/goDown`, {
          value: this.Components.length
        });
      }
    },
  },
};
</script>

<style lang="sass" scoped>
div
  div
    position: fixed
    opacity: .7
    right: 5vh

    img
      width: 85px
      cursor: pointer

    span
      font-size: 22px
      padding: 10px
      color: #FFF
      display: inline

    .up
      top: 5vh
      transition: .5s

      &:active
        top: 1vh

    .down
      bottom: 5vh
      transition: .5s

      &:active
        bottom: 1vh

@media (max-width: 769px)
  div
    div
      right: 10px

      img
        width: 65px

      span
        display: none

@media (max-width: 767px)
  div
    div
      right: 1px

      img
        width: 35px

      span
        display: none
</style>
